// @flow

import { assert } from 'chai'
import { describe, it } from 'mocha'

import { search } from '../../src/utils/jsonSearch.js'
const jsonObj = {
  alpha: 1,
  // bravo: [1, 2, 3],
  charlie: { delta: 1 },
  india: {
    kilo: 4,
    lima: 4
  },
  uniform: {
    kilo: 4,
    lima: 4
  },
  echo: {
    // foxtrot: [1, 2, 3],
    golf: {
      india: {
        kilo: 4,
        lima: 4
      },
      juliet: {
        kilo: 4,
        lima: 5
      },
      // mike: [
      //   { kilo: 4 },
      //   6,
      //   { lima: 5 },
      //   { lima: 6, kilo: 7 },
      //   { charlie: { delta: 1 } },
      //   { alpha: 1 },
      //   [ 6, { lima: 5 }, 10 ]
      // ],
      november: 8
    }
  }
}

const fixtures = [
  [ [], jsonObj ],
  [ ['alpha', 1], jsonObj ],
  [ ['alpha', 2], null ],
  [ ['charlie', 1], null ],
  [ ['charlie.delta', 1], jsonObj['charlie'] ],
  [ ['india.kilo', 5], null ],
  [ ['india.lima', 5], null ],
  [ ['echo.golf.india.kilo', 4], jsonObj['echo']['golf']['india'] ],
  [ ['echo.golf.india.kilo', 5], null ],
  [ ['echo.golf.november', 8], jsonObj['echo']['golf'] ],

  [ ['india.kilo', 4], jsonObj['india'] ]

  // ['*.lima', 5],
  // ['*.lima', 4],
  // ['kilo', 4],
]

describe('Testing Json Search', function () {
  fixtures.forEach(([ test, expected ]) => {
    it(`Testing search with: ${JSON.stringify(test)} `, function () {
      const searchParams = {
        keyToMatch: test[0],
        valueToMatch: test[1]
        // keyToReturn: test[2]
      }
      const results = search(searchParams, jsonObj)
      assert.deepEqual(results, expected)
      console.log(results)
    })
  })
})
